export const query = graphql`
  query storeBySlug($slug: String) {
    allStrapiStores(
      filter: {
        slug: { eq: $slug }
        isActive: { eq: true }
        isApproved: { eq: true }
      }
    ) {
      nodes {
        id
        slug
        BusinessStartedYear
        about
        aboutTa
        heading
        headingTa
        placeId
        latitude
        longitude
        maxAmount
        minAmount
        phoneNo1
        pincode
        emailId
        districtTa
        district
        locationShort
        locationShortTa
        addressFullTa
        addressFull
        categories {
          id
          label
          labelTa
          name
        }
        location {
          id
          label
          labelTa
          name
        }
        gallery {
          image {
            childImageSharp {
              fluid(maxWidth: 500, quality: 100) {
                ...GatsbyImageSharpFluid_withWebp
              }
              fixed(height: 100, quality: 100) {
                ...GatsbyImageSharpFixed_withWebp
              }
            }
          }
        }
        logo {
          childImageSharp {
            fluid(maxWidth: 500, quality: 100) {
              ...GatsbyImageSharpFluid_withWebp
            }
            # fixed(width: 200, quality: 100) {
            #   ...GatsbyImageSharpFixed_withWebp
            # }
          }
        }
      }
    }
  }
`;

export default injectIntl(VendorMainPage);
